<template>
  <div>
    <b-form @submit.stop.prevent="onSubmit">
        <b-row>
            <b-col>
                <!-- Input Name -->
                <b-form-group id="example-input-group-1" label="Name" label-for="example-input-1">
                    <b-form-input
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.name.$model"
                    :state="validateState('name')"
                    aria-describedby="input-1-live-feedback"
                    ></b-form-input>

                    <b-form-invalid-feedback
                    id="input-1-live-feedback"
                    >This is a required field and must be at least 3 characters.</b-form-invalid-feedback>
                </b-form-group>

                    <!-- Input Last Name -->
                <b-form-group id="example-input-group-1" label="Last name" label-for="example-input-1">
                    <b-form-input
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.lastname.$model"
                    :state="validateState('lastname')"
                    aria-describedby="input-1-live-feedback"
                    ></b-form-input>

                    <b-form-invalid-feedback
                    id="input-1-live-feedback"
                    >This is a required field and must be at least 3 characters.</b-form-invalid-feedback>
                </b-form-group>

                    <!-- Input Phone  -->
                <b-form-group id="example-input-group-1" label="Phone" label-for="example-input-1">
                    <b-form-input
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.phone.$model"
                    :state="validateState('phone')"
                    aria-describedby="input-1-live-feedback"
                    type="number"
                    ></b-form-input>

                    <b-form-invalid-feedback
                    id="input-1-live-feedback"
                    >This is a required field and must be at least 10 characters.</b-form-invalid-feedback>
                </b-form-group>

            </b-col>

            <b-col>
        <!-- Input Email -->
                <b-form-group id="example-input-group-1" label="Email" label-for="example-input-1">
                    <b-form-input
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.email.$model"
                    :state="validateState('email')"
                    aria-describedby="input-1-live-feedback"
                    type="email"
                    ></b-form-input>

                    <b-form-invalid-feedback id="input-1-live-feedback"> This is a required field. </b-form-invalid-feedback>
                </b-form-group>

                
                <!-- Input Message -->
                <b-form-group id="example-input-group-1" label="Message" label-for="example-input-1">
                    <b-form-textarea
                    id="example-input-1"
                    name="example-input-1"
                    v-model="$v.form.message.$model"
                    :state="validateState('message')"
                    aria-describedby="input-1-live-feedback"
                    rows="3"
                    max-rows="6"
                    ></b-form-textarea>

                    <b-form-invalid-feedback id="input-1-live-feedback"> This is a required field. </b-form-invalid-feedback>
                </b-form-group>


            <b-form-group id="example-input-group-2" label="Services" label-for="example-input-2">
                <b-form-select
                id="example-input-2"
                name="example-input-2"
                v-model="$v.form.service.$model"
                :options="services"
                :state="validateState('service')"
                aria-describedby="input-2-live-feedback"
                ></b-form-select>

                <b-form-invalid-feedback id="input-2-live-feedback">This is a required field.</b-form-invalid-feedback>
            </b-form-group>

            <b-button type="submit" variant="primary">Submit</b-button>
            <b-button class="ml-2" @click="resetForm()">Reset</b-button>
            </b-col>
        </b-row>
    </b-form>
  </div>
</template>

<style>
body {
  padding: 1rem;
}
</style>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength } from "vuelidate/lib/validators";

export default {
  mixins: [validationMixin],
  data() {
    return {
      services: [
        { value: null, text: 'I’m interested in service' },
        { value: 'Dental Implants', text: 'Dental Implants' },
        { value: 'Braces', text: 'Braces' },
        { value: 'Crowns and Bridges', text: 'Crowns and Bridges' },
        { value: 'Endodontics', text: 'Endodontics' },
        { value: 'Fillings and Extractions', text: 'Fillings and Extractions' }
      ],
      form: {
        name: null,
        service: null,
        email: null,
        lastname: null,
        phone: null,
        message: null
      }
    };
  },
  validations: {
    form: {
      service: {
        required
      },
      name: {
        required,
        minLength: minLength(3)
      },
      email: {
        required,
      },
      lastname: {
        required,
      },
      phone: {
        required,
        minLength: minLength(10)
      },
      message:{
        required,
        minLength: minLength(1500)
      },
    }
  },
  methods: {
    validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },
    resetForm() {
      this.form = {
        name: null,
        food: null
      };

      this.$nextTick(() => {
        this.$v.$reset();
      });
    },
    onSubmit() {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }

      alert("Form submitted!");
    }
  }
};
</script>